import { SearchModule } from './search/search.module';
import { PaymentButton2Module } from './payment-button2/payment-button2.module';
import { InfoBill2Module } from './info-bill2/info-bill2.module';
import { BillDetail2Module } from './bill-detail2/bill-detail2.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from './../../../ng-zorro-ant.module';
import { ReactiveFormsModule , FormsModule} from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { CartComponent } from './cart.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

const routes: Routes = [
  {
    path: '',
    component:  CartComponent,
  },
]

@NgModule({
  declarations: [CartComponent, ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    BillDetail2Module,
    SearchModule,
    InfoBill2Module,
    PaymentButton2Module
  ]
})
export class CartModule { }
