import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { FunctionService } from 'src/app/cafe/services/function.service';

@Component({
  selector: 'app-search-service2',
  templateUrl: './search-service2.component.html',
  styleUrls: ['./search-service2.component.scss']
})
export class SearchService2Component implements OnInit {
  // chng trnh khuyn mi ang c p dng
  @Input("promotions") promotions: any[] = []
  // tr v product c chn v  x l cho search
  @Output("closeSearchServiceList") closeSearchServiceList: EventEmitter<any> = new EventEmitter();
  /** Danh sch dch v */
  public serviceList: any[] = [];
  /** Dch v ang c x l */
  choiceService: any = {}
  /** Danh sch dch v  c tm kim */
  public searchList: any[] = [];
  /** Danh mc dch v */
  list_menu: any = [];
  listTabs: Array<any> = [{ _id: 'all', name: 'All', type: 1 }]
  @Input() default_price_type: number
  indexTab: number = 0;

  public VhType = VhType;
  
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private functionService: FunctionService,
    public vhAuth : VhAuth
  ) { }

  ngOnInit(): void {
    this.getMenu()
  }

  getMenu() {
    this.serviceList = this.vhQueryCafe.getlocalDetailProducts(VhType.SERVICE_SERVICETIME).filter(item => !item.delete_hidden).map(e => {return {...e, ...e.units[0]}})
    this.serviceList = this.vhAlgorithm.sortVietnamesebyASC(this.serviceList,'name')
    this.list_menu = this.vhQueryCafe.getlocalCategorys().map(e =>{
      e.status = this.serviceList.find(pro => pro.id_category == e._id) ? true : false
      return {...e}
    })
    this.list_menu.filter(cate => cate.status).forEach(item => this.listTabs.push(item));

    this.searchList = this.functionService.transformProd5(this.serviceList);
  }

  @Input() search_value = ''
  ngOnChanges(changes: SimpleChanges): void {
    this.serviceList = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(this.search_value.toLocaleLowerCase()),  this.vhQueryCafe.getlocalDetailProducts(VhType.SERVICE_SERVICETIME).filter(item => !item.delete_hidden), ['name']).map(e => {return {...e, ...e.units[0]}})
    this.serviceList = this.vhAlgorithm.sortVietnamesebyASC(this.serviceList,'name')
    this.searchList = this.functionService.transformProd5(this.serviceList); 
  }
  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  goBack() {
    this.closeSearchServiceList.emit(false);
  }
  /**
   * Chn dch v x l
   * @param value thng tin dch ang c x l
   */
  selectService(value) {
    // this.checkPromotions(value._id)
    //   .then((promotionProd: any) => {
      let dispatcher_done = !this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").dispatchers_enable
      this.choiceService = {
        ...value, 
        quantity: 1, 
        note: "", 
        price_origin: this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price,
        price: this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price,
        ptype: value.type,
        dispatcher_done: dispatcher_done
      }

      if (this.choiceService) {
        if (value.type !== VhType.SERVICETIME) {
          this.showOptions = true;
          setTimeout(() => {
            this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(".price-" + value._id)
          }, 200)
        }
        else {
          this.closeSearchServiceList.emit(this.choiceService);
        }
      }
      // })
  }

  editPrice: any = {}
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue()
    this.choiceService.price = price ? parseFloat(price) : 0;
    this.choiceService.quantity = this.choiceService.quantity > 0 ? this.choiceService.quantity : 1
  }
  chooseService() {
    this.showOptions = false;
    if (this.choiceService['quantity']) {
      this.closeSearchServiceList.emit(this.choiceService)
    }
  }

  changeTypeGood(event) {
    this.indexTab = event
    let products = this.vhQueryCafe.getlocalDetailProducts(VhType.SERVICE_SERVICETIME).filter(item => !item.delete_hidden).map(e => {return {...e, ...e.units[0]}})
    if (event == 0) {
      this.serviceList = products
    }
    else {
      this.serviceList = products.filter(item => item.id_category == this.listTabs[event]._id);
    }
    this.searchList = this.functionService.transformProd5(this.serviceList)
  }
  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }


  increaseQuantityToppings(item){
    if(item.quantity != '') item.quantity = parseFloat(item.quantity)
    item.quantity = item.quantity + 1
  }
  reduceQuantityToppings(item){
    if(item.quantity != '') item.quantity = parseFloat(item.quantity)
    item.quantity = item.quantity - 1
  }
}
