import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { FunctionService } from 'src/app/cafe/services/function.service';

@Component({
  selector: 'app-search-topping2',
  templateUrl: './search-topping2.component.html',
  styleUrls: ['./search-topping2.component.scss']
})
export class SearchTopping2Component implements OnInit {
  // chng trnh khuyn mi ang c p dng
  @Input("promotions") promotions: any[] = []
  // tr v product c chn v  x l cho search
  @Output("closeSearchSideDishesList") closeSearchSideDishesList: EventEmitter<any> = new EventEmitter();
  /** Danh sch mn ph */
  public sideDishesList: any[] = [];
  /** Danh sch mn ph ang c tm kim */
  public searchList: any[] = [];
  /** Mn ph ang c chn */
  choiceSideDishes: any = {}
  @Input() default_price_type: number
  // bien kim tra show bn phm o hay ko
  show_keyboard: boolean = this.vhAuth.localStorageGET("show_keyboard");

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private functionService: FunctionService,
    private vhAuth:VhAuth
  ) { }
  ngOnInit(): void {
    this.sideDishesList = this.vhQueryCafe.getlocalProducts().filter(item => !item.delete_hidden && item.type == 4).map(e => {return {...e, ...e.units[0]}})
    this.sideDishesList =this.vhAlgorithm.sortVietnamesebyASC(this.sideDishesList,'name')
    this.searchList = this.functionService.transformProd5(this.sideDishesList);
  }


  @Input() search_value = ''
  ngOnChanges(changes: SimpleChanges): void {
    this.sideDishesList = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(this.search_value.toLocaleLowerCase()),  this.vhQueryCafe.getlocalProducts().filter(item => !item.delete_hidden && item.type == 4), ['name']).map(e => {return {...e, ...e.units[0]}})
    this.sideDishesList =this.vhAlgorithm.sortVietnamesebyASC(this.sideDishesList,'name')
    this.searchList = this.functionService.transformProd5(this.sideDishesList); 
  }
  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  goBack() {
    this.closeSearchSideDishesList.emit(false);
  }

  /** Chn mn ph  x l
   * @param value: thng tin mn ph c chn
   */
  selectSideDishes(value) {
    // this.checkPromotions(value._id) // kim tra chng trnh khuyn mi p dng
    //   .then((promotionProd: any) => {
      let dispatcher_done = !this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").dispatchers_enable
        this.choiceSideDishes = {
          ...value, quantity: 1, note: "", price_origin: this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price,
           price : this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price,ptype : 4, dispatcher_done : dispatcher_done
        }
        if (this.choiceSideDishes) {
          this.showOptions = true;
          setTimeout(() => {
            this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(".price-" + value._id)
          }, 200)
        }
      // })
  }

  editPrice: any = {}
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue()
    this.choiceSideDishes.price = price ? parseFloat(price) : 0;
    this.choiceSideDishes.quantity = this.choiceSideDishes.quantity > 0 ? this.choiceSideDishes.quantity : 1
  }
  chooseSideDishes() {
    this.showOptions = false;
    if (this.choiceSideDishes['quantity']) {
      this.closeSearchSideDishesList.emit(this.choiceSideDishes)
    }
  }

  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }



  increaseQuantityToppings(item){
    if(item.quantity != '') item.quantity = parseFloat(item.quantity)
    item.quantity = item.quantity + 1
  }
  reduceQuantityToppings(item){
    if(item.quantity != '') item.quantity = parseFloat(item.quantity)
    item.quantity = item.quantity - 1
  }
}
