import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-bill1',
  templateUrl: './bill1.component.html',
  styleUrls: ['./bill1.component.scss']
})
export class Bill1Component implements OnInit, AfterViewInit, OnChanges {
  // i tng ha n
  @Input() invoices: VhOrderInvoices
  // khch hng hin ti c c n hay khng?
  @Input() debt: boolean = false;
  // gi tr thu hin ti
  @Input() tax: number = 0
  @Input()  customer_class: any;
  // phng thc thanh ton hin ti - cu trc d liu tng thch vi component ang c s dng
  @Input() paymentType: Array<any> = [1]
  @Output() updateInvoice: EventEmitter<boolean> = new EventEmitter()
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ]
  /** Danh sch thu c to v ang c kch hot */
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  moneyEdit: any = {}
  /**bin lu thng tin bill - discount ,fee, payment, cash, note- dng  gn gi tr khi dng bn phim o */
  invoice_discount: string;
  invoice_fee: string;
  invoice_payment: string;
  invoice_cash: string;
  invoice_note: string;
  percent_discount : number = 0;
  is_discount_bill : boolean = false;
  constructor(
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    public vhAuth:VhAuth,
    private vhComponent:VhComponent
  ) { }

  ngOnInit(): void {
    // ly thng tin ti khon
    this.getWallets()
     
  }

  ngOnChanges({ debt }: SimpleChanges) {
    if (debt) {
      if (!debt.currentValue) {
        if (this.paymentType[0] == 2) this.changePaymentType([1])
      }
      this.nzOptions[1].disabled = !debt.currentValue
    }
    this.invoice_discount = this.invoices.getDiscount().toString();
    this.invoice_fee = this.invoices.getFee().toString();
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
    this.invoice_note = this.invoices.getNote();
    this.percent_discount = this.invoices.getSubTotal() ? this.invoices.getDiscount()/this.invoices.getSubTotal()*100 : 0
     
  }

  ngAfterViewInit() {
    // Cleave js
    if(document.querySelector(".discount-price")) this.moneyEdit['discount'] = this.vhAlgorithm.vhnumeral(".discount-price")
    if(document.querySelector(".fee-price")) this.moneyEdit['fee'] = this.vhAlgorithm.vhnumeral(".fee-price")
    if(document.querySelector(".payment-price")) this.moneyEdit['payment'] = this.vhAlgorithm.vhnumeral(".payment-price")
    if(document.querySelector(".customerPay")) this.moneyEdit['cash'] = this.vhAlgorithm.vhnumeral(".customerPay")
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true }
      })
  }

  editPercentDiscount(value) {
    if (parseFloat(value) > 100)
      document.getElementById("percent-discount-price")['value'] = 0
    else this.changePercentDiscount(value)
  }
  changePercentDiscount(value){
    this.percent_discount = value
    this.invoices.setDiscount(this.invoices.getSubTotal()*value/100)
    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
    this.invoice_discount = (this.invoices.getDiscount()).toString();
  }

  /**Cp nht gim gi */
  updateDiscount() {
    let discount: any = this.moneyEdit['discount'].getRawValue()
    this.invoices.setDiscount(discount ? parseFloat(discount) : 0)
    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
    this.percent_discount = discount/this.invoices.getSubTotal()*100
  }
  /** Cp nht ph */
  updateFee() {
    let fee: any = this.moneyEdit['fee'].getRawValue()
    this.invoices.setFee(fee ? parseFloat(fee) : 0)
    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
  }
  /**Cp nht thu */
  updateTax(value) {
    this.invoices.setTax(parseFloat(value))
    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
  }
  /**Cp nht tin khch tr */
  updatePayment() {
    if (this.debt) {
      let payment: any = this.moneyEdit['payment'].getRawValue()
      let value = payment ? parseFloat(payment) : 0

      this.invoices.setPayment(value ? (value > this.invoices.getTotalATax() || value < 0 ? this.invoices.getTotalATax() : value) : 0)

      document.getElementById("payment-price")['value'] = this.invoices.getPayment()
      this.moneyEdit['payment'] = this.vhAlgorithm.vhnumeral(".payment-price")

      this.updateInvoice.emit(true);
      this.invoice_payment = this.invoices.getPayment().toString();
      this.invoice_cash = this.invoices.getCash().toString();
    }
  }
  /**Cp nht ghi ch */
  updateNote() {
    let note: any = document.getElementById("invoice-note")
    this.invoices.setNote(note.value)
    this.updateInvoice.emit(true)
  }

  /**Cp nht tin khch tr*/
  updateCash() {
    this.invoices.setCash(parseFloat(this.moneyEdit['cash'].getRawValue()))
    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
  }

  /** Thay i phng thc thanh ton 
   * @param value loi thanh ton
  */
  changePaymentType(value: any) {
    this.invoices.setPaymentType(value[0])
    this.invoices.setPayment()
    switch (value[0]) {
      case 1:
        this.invoices.setIdWallet("id_cash")
        break
      case 2:
        this.invoices.setIdWallet("");
        this.invoices.setCash(0);
        this.invoice_cash = '0';
        break;

      case 3:
        this.invoices.setIdWallet(value[1])
        break;
    }

    this.updateInvoice.emit(true);
    this.invoice_payment = this.invoices.getPayment().toString();
    this.invoice_cash = this.invoices.getCash().toString();
  }

  /**
   * kim tra ti a 12 ky t cu discount
   * @param value 
   */
  checkMaxLengthDiscount(value: string) {
    if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_discount = value }
  }

  /**
   * kim tra ti a 12 ky t cu fee
   * @param value 
   */
  checkMaxLengthFee(value: string) {
    if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_fee = value }
  }

  /**
   * kim tra ti a 12 ky t cu payment
   * @param value 
   */
  checkMaxLengthPayment(value: string) {
    if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_payment = value }
  }

  /**
     * kim tra ti a 12 ky t cu cash
     * @param value 
     */
  checkMaxLengthCash(value: string) {
    if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_cash = value }
  }

  
  showModalOtherDiscount = false
  openModalOtherDiscount() {
    this.showModalOtherDiscount = true;
    this.point_validity =  this.vhQueryCafe.getlocalCustomerPoints(this.invoices.getIdPartner())
  }

  handleOkOtherDiscount() {
    if (this.invoices.getPayment() < this.invoices.getDiscountOther()) this.invoices.setPayment(0);
    this.invoices.setArrayCouponCodes(this.product_codes)
    this.showModalOtherDiscount = false;
    // this.updateInvoice.emit(true);
  }
  point_validity  = 0
  value_discount_point: number = 0;
  product_codes: any = [];
  getDiscountOfCoupon(code) {
    if (code)
      this.vhQueryCafe.getCoupon_Code_Name_Release_byCode(code)
        .then((product_codes: any) => {
          if (product_codes && !this.product_codes.find(item => item.code == code)) {
            switch (product_codes.status) {
              case 1: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny cha c quy i"))
                break;
              }
              case 3: {
                if (new Date() < new Date(product_codes.date_expire)) {
                  this.product_codes.push(product_codes)
                  this.invoices.setDiscountCoupon(product_codes.value)
                  this.vhComponent.alertMessageDesktop("success", this.languageService.translate("S dng coupon thnh cng"))
                }
                else {
                  this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny  ht hn s dng"), 8000)
                }
                break;
              }
              case 4: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Phiu ny  c s dng"), 8000)
                break;
              }
            }
          }
          if (!product_codes) this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Khng tm thy phiu ny"), 8000)
          setTimeout(() => {
            document.getElementById('coupon').getElementsByTagName("input")[0].value = '';
          }, 200);
        })

  }

  onCloseProductCode(item) {
    this.product_codes.splice(this.product_codes.findIndex(i => i._id == item._id), 1);
    this.invoices.setDiscountCoupon(-item.value)
  }
  calculatePoint2Money(value) {
    if (value <= this.point_validity) {
      this.value_discount_point = value;
      this.invoices.setDiscountPoint((value / this.customer_class.payment.points) * this.customer_class.payment.money)
      this.invoices.setUsePoint(value)
    }
    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vt qu gii hn im cho php"))
    }
  }
}
