import { Component, HostListener, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F3", "F4", "F5", "F6"].includes(event.key)) {
      this.closeSearchProduct(false)
      this.closeSearchService(false)
      this.closeSearchSideDishes(false)
      this.closeSearchCombos(false)

      switch (event.key) {
        case "F3": this.typeProduct = 1; break;
        case "F4": this.typeProduct = 2; break;
        case "F5": this.typeProduct = 4; break;
        case "F6": this.typeProduct = 5; break;
      }
      event.preventDefault(); event.stopPropagation()
    }
  }
  // tr v product c chn v  x l cho bill-detail2
  @Output() billDetail: EventEmitter<boolean> = new EventEmitter();
  @Output() goback = new EventEmitter();
  typeProduct: 1 | 2 | 4 | 5 = 1
  showDrawerProductList: boolean = false
  showDrawerServiceList: boolean = false
  showDrawerSideDishesList: boolean = false
  showDrawerCombosList: boolean = false
  showModalChangePriceType: boolean = false;
  @Input() default_price_type: any;
  @Output() changePriceType: EventEmitter<any> = new EventEmitter()
  @Output() checkPromotion: EventEmitter<any> = new EventEmitter()
  management_product_price2: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_price2
  search_value = ''
  constructor(
    private vhQueryCafe: VhQueryCafe,
    public vhAuth: VhAuth
  ) { }

  ngOnInit(): void {
  }

  emitGoBack() {
    this.goback.emit(true)
  }

  closeSearchProduct(ev) {
    if (ev) this.billDetail.emit(ev)
    else this.showDrawerProductList = false
  }
  closeSearchService(ev) {
    if (ev) this.billDetail.emit(ev)
    else this.showDrawerServiceList = false
  }
  closeSearchSideDishes(ev) {
    if (ev) this.billDetail.emit(ev)
    else this.showDrawerSideDishesList = false
  }
  closeSearchCombos(ev) {
    if (ev) this.billDetail.emit(ev)
    else this.showDrawerCombosList = false
  }
  handleOk() {
    this.showModalChangePriceType = false
    this.changePriceType.emit(this.default_price_type)
  }

  handleCancel() {
    this.showModalChangePriceType = false
    this.changePriceType.emit(null)
  }
}
